.page {
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-weight: 400;
}

.head {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  display: flex;
}

.head__logo {
  height: 40px;
  margin-left: 50px;
}

.nav {
  margin-right: 50px;
}

.nav__link {
  color: #060b35;
  flex-direction: column;
  justify-content: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  line-height: 14px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__item {
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  display: flex;
}

.nav__item:nth-child(n+2) {
  margin-left: 20px;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.is-active {
  color: #00acdc;
  position: relative;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.logo {
  display: block;
}

.hedBox {
  flex-direction: row;
  place-content: center;
  align-items: center;
  width: 100%;
  margin: 0 50px;
  display: flex;
}

.container {
  --column-count: 1;
  --rows-count: 8;
  grid-template-columns: repeat(var(--column-count), 200px);
  grid-template-rows: repeat(var(--rows-count), 408px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 448px) {
  .container {
    --column-count: 2;
    --rows-count: 4;
  }
}

@media (width >= 768px) {
  .container {
    --column-count: 3;
    --rows-count: 3;
  }
}

@media (width >= 1024px) {
  .container {
    --column-count: 4;
    --rows-count: 2;
  }
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  height: 408px;
  padding: 16px;
  transition: all .3s;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 16px 0 0;
  transition: all .3s;
}

.card__title {
  color: #060b35;
  width: 100%;
  margin: 40px 0 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: all .3s;
}

.card__title:hover {
  color: #00acdc;
  transition: all .3s;
}

.card__product-code {
  color: #616070;
  width: 100%;
  margin: 4px 0 0;
  font-size: 10px;
  line-height: 14px;
  transition: all .3s;
}

.card__rating {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 16px 0 0;
  display: flex;
}

.card__revievs {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  line-height: 14px;
  transition: all .3s;
}

.card__price-container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 18px;
  margin: 24px 0 0;
  transition: all .3s;
  display: flex;
}

.card__price-label {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
  transition: all .3s;
}

.card__price {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
  transition: all .3s;
}

.card__buy {
  box-sizing: border-box;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin: 16px 0 0;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  width: 240px;
  height: 452.88px;
}

.card:hover .card__image {
  width: 192px;
  height: 160.8px;
  margin: 16px 0 0;
}

.card:hover .card__title {
  font-size: 14.4px;
  line-height: 21.6px;
}

.card:hover .card__product-code {
  margin: 4px 0 0;
  font-size: 12px;
  line-height: 16.8px;
}

.card:hover .card__revievs {
  margin: 0;
  font-size: 12px;
  line-height: 16.8px;
}

.card:hover .stars__star {
  background-size: 100%;
  width: 19.2px;
  height: 19.2px;
}

.card:hover .card__price-label {
  font-size: 14.4px;
  line-height: 21.6px;
}

.card:hover .card__price {
  font-size: 19.2px;
  line-height: 21.6px;
}

.card:hover .card__buy {
  border-radius: 5px;
  width: 100%;
  height: 48px;
  margin: 16px 0 0;
  font-size: 16.8px;
  line-height: 19.2px;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
  transition: all .3s;
}

.stars__star:last-child {
  margin: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

